<template>
  <div class="back">
    <app-bar
      back-icon
      title="公告栏"
      hideMessage
      hideDemand
      hideUserMenu
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mt-12" min-height="400" max-width="1000">
          <p class="text-center pt-8 headline">{{ context.title }}</p>
          <div class="dotted_line mt-3 mx-5"></div>
          <p class="text-center grey--text text--darken-2 body-2 mt-5">
            <span class="mr-3">发布者: {{ context.publisher }}</span>
            <span class="mx-3">发布时间：{{ context.time }}</span>
            <span class="ml-3">点击量: {{ context.click }}</span>
          </p>
          <div class="mx-8">
            <p v-html="context.content">{{ context.content }}</p>
          </div>
          <p class="text-left mx-12 pt-4" v-if="context.fileURL">
            <v-icon>mdi-download</v-icon
            ><span class="text-left">附件下载：</span>
            <a :href="context.fileURL" :download="filename">{{ filename }}</a>
          </p>
        </v-card>
      </v-container>
    </v-content>
    <AppFooter></AppFooter>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'New',
  props: ['id'],
  components: {
    AppBar,
    AppFooter,
  },
  watch: {
    $route() {
      this.getNew()
    },
  },
  data() {
    return {
      context: {
        title: '',
        publisher: '',
        time: '',
        content: '',
        click: '',
        fileURL: '',
      },
      filename: '',
      last: {},
      next: {},
    }
  },
  methods: {
    async getNew() {
      let res = await this.$http.get('/api/home/announce?id=' + this.id)
      if (res.status !== 200) return false
      if (res.data.code === 200) {
        this.context = res.data.data.announce
        this.filename = res.data.data.filename
      }
    },
  },
  mounted() {
    this.getNew()
  },
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
.dotted_line {
  width: 95%;
  height: 0;
  border-bottom: #c6c6c6 1px dashed;
}
</style>
